<template>
  <div id="map">
  </div>
</template>
 
<script>
import BMap from 'BMap'
export default {
  name: 'Map',
  data () {
    return {
    }
  },
  methods: {
    createMap () {
      /* eslint-disable */
      // Map
      var map = new BMap.Map("map")
      // ,
      map.centerAndZoom(new BMap.Point(119.176895, 26.072713), 15)
      //
      map.addControl(new BMap.MapTypeControl({
        mapTypes:[BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
      }))
      //  
      map.setCurrentCity("")
      //
      map.enableScrollWheelZoom(true)
      /* eslint-enable */
    }
  },
  mounted () {
    this.createMap()
  }
}
</script>
<style scoped >
#map{
  min-height:50vh;
  min-width:50vw;
}
</style>